@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;400;700&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: url("https://images.unsplash.com/photo-1621274403997-37aace184f49?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1");
  background-repeat: no-repeat;
  background-size: cover;
  overflow-y: scroll;
  height: 110vh;
  font-family: "Poppins", sans-serif;
}

.container {
  padding: 20px 70px;
  color: #fff;
}

.search-bar {
  text-align: center;
  max-width: 100%;
  max-height: 100px;
}
.search-bar input[type="text"] {
  width: 50%;
  padding: 10px;
  color: #fff;
  background: rgba(0, 0, 0, 0.8);
}
.search-bar button {
  border: 1px solid #fff;
  padding: 10px;
  background: rgba(0, 0, 0, 0.8);
  color: #fff;
  border-radius: 3px;
}
.search-bar button:hover {
  background: rgba(255, 255, 255, 0.8);
  color: black;
}

.current-info {
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.date-container {
  font-weight: 50;
}
.date-container .time {
  font-size: 35px;
  padding: 2px;
  border-radius: 10%;
  display: flex;
  align-items: center;
}

.date-container #am-pm {
  font-size: 30px;
}

.date-container .date {
  font-size: 20px;
}

.place-container {
  border-radius: 10%;
  min-width: 55%;
  background: rgba(0, 0, 0, 0.8);
  border: none;
  background-size: contain;
}
.hourly-forecast {
  width: 100%;
}
.current-info .others {
  display: flex;
  flex-direction: column;
  background: rgba(0, 0, 0, 0.8);
  padding: 10px;
  border-radius: 10px;
  margin: 10px 0;
  border: 1px solid #eee;
}

.current-info .others .weather-item {
  display: flex;
  justify-content: space-between;
}

.future-forecast {
  background: rgba(24, 24, 27, 0.8);
  padding: 10px;
  /* position: ; */
  bottom: 0;
  display: flex;
  color: white;
  width: 100%;
  align-items: center;
  justify-content: center;
  overflow-y: hidden;
}

.future-forecast .weather-forecast {
  display: flex;
}

.weather-forecast .weather-forecast-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0 10px;
  border: 1px solid #eee;
  padding: 15px;
  border-radius: 10px;
  background: rgba(0, 0, 0, 0.8);
}

.weather-forecast .weather-forecast-item .day {
  padding: 5px 15px;
  background: #3c3c44;
  border-radius: 50px;
  text-align: center;
}

.weather-forecast .weather-forecast-item .temp {
  font-weight: 100;
  font-size: 12px;
}

@media only screen and (max-width: 730px) {
  .container {
    padding: 20px;
  }

  .future-forecast {
    justify-content: start;
    align-items: none;
    overflow-y: scroll;
  }

  .future-forecast .today .temp {
    font-size: 16px;
  }

  .date-container .time {
    font-size: 50px;
  }

  .date-container #am-pm {
    font-size: 20px;
  }

  .date-container .date {
    font-size: 20px;
  }

  .place-container {
    text-align: end;
    margin-top: 15px;
  }

  .place-container .time-zone {
    font-size: 20px;
  }

  .current-info .others {
    padding: 12px;
  }

  .current-info .others .weather-item {
    font-size: 14px;
  }
}

@media only screen and (max-width: 1400px) {
  .future-forecast {
    justify-content: start;
    align-items: none;
    overflow-x: scroll;
  }
  .future-forecast::-webkit-scrollbar {
    display: none;
  }
}

